<template>
  <div class="col-11 p-2 mx-auto col-md-6">
    <div class="Box">
      <div class="Box-header">
        <h3 class="Box-title">
          Choose database
        </h3>
      </div>
      <div class="Box-body" v-for="db in databases">
        <router-link :to="`/database/${db}`">{{db}}</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import wsp from "@/api/Socket";

export default {

  data() {
    return {
      databases: []
    }
  },

  async mounted() {
    await wsp.open()
    this.databases = (await wsp.sendRequest({type: "databases"})).data
    if (this.databases.length === 1) {
      await this.$router.replace(`/database/${this.databases[0]}`)
    }
  },
}
</script>