<template>
  <div style="height: 100%;overflow: scroll">
    <TableFilters
        :columns="columns"
        :apply-filters="applyFilters"
        :database="$route.params.database"
        :table="$route.params.table"
        ref="tableFilters"/>
    <div class="table-wrapper" :style="`height: calc(100% - ${tableFilterHeight}px)`">
      <div class="table-data"/>
    </div>
    <LimitOffset
        v-model="limit"
        :table-rows-count="rowsCount"
        :reset-order-type-limit="resetOrderTypeLimit"
    />
  </div>
</template>

<script>
import Socket from '@/api/Socket'
import Row from "@/views/Row";
import TableHeader from "@/components/TableHeader"
import TableFilters from "@/components/TableFilters";
import LimitOffset from "@/components/LimitOffset";
import sql from "@/utils/sql";
import Tabulator from 'tabulator-tables'
import defaultValue from "@/utils/default-value";

const columnWidthMap = {
  bool: 50,
  varchar: 200,
  VARCHAR: 200,
  text: 200,
  TEXT: 200,
  timestamp: 100,
  int8: 100,
  int4: 100,
  INTEGER: 100,
  REAL: 100
}

export default {

  components: {Row, TableHeader, TableFilters, LimitOffset},

  data() {
    return {
      key: this.$route.fullPath,
      columns: [],
      columnTypes: {},
      rowsCount: 0,
      tableData: null,
      skippedFirstLimitChange: false,
      limit: 20,
      offset: 0,
      order: 1,
      type: 'asc',
      clause: null,
    }
  },

  mounted() {
    if (this.table) this.setup()
  },

  computed: {
    table() {
      const {table} = this.$route.params
      return this.$store.state.tables.find(t => t.tableName === table)
    },

    tableFilterHeight() {
      return this.$store.state.tableFiltersHeight
    }
  },

  watch: {
    // set watcher for notify then tables will be loaded after reload page
    table() {
      this.setup()
    },

    limit() {
      // if (!this.skippedFirstLimitChange) {
      //   return this.skippedFirstLimitChange = true
      // }
      const {database, table} = this.$route.params
      this.$store.commit('updateByKey', {database, table, key: 'limit', value: this.limit})
      this.tableData.setPageSize(this.limit)
      this.tableData.setMaxPage(Math.round(this.rowsCount / this.limit))
    },

    order() {
      const {database, table} = this.$route.params
      this.$store.commit('updateByKey', {database, table, key: 'order', value: this.order})
    },

    type() {
      const {database, table} = this.$route.params
      this.$store.commit('updateByKey', {database, table, key: 'type', value: this.type})
    }
  },

  methods: {
    // setup must be called only after tables fully loaded
    setup() {
      console.log("setup")
      const {database} = this.$route.params
      this.columns = this.table.columns.sort(c => c.cid)
      this.table.columns.forEach(c => this.columnTypes[c.name] = c.type)
      this.rowsCount = this.table.count
      this.limit = this.$store.getters.getLimit(database, this.table.tableName)
      this.order = this.$store.getters.getByKey(database, this.table.tableName, 'order') || '1'
      this.type = this.$store.getters.getByKey(database, this.table.tableName, 'type') || 'asc'

      const filters = this.$store.getters.getFilters(database, this.table.tableName)
      if (filters?.length > 0) this.clause = sql.provider.selectQuery(this.table.tableName, filters)


      this.tableData = new Tabulator('.table-data', {
        height: `100%`,
        layout: "fitColumns",
        placeholder: "No Data Set",
        columnHeaderSortMulti: false,
        ajaxSorting: true,
        ajaxLoader: false,
        ajaxURL: "https://local",
        ajaxFiltering: true,
        paginationSize: this.limit,
        paginationInitialPage: 1,
        pagination: 'remote',
        ajaxRequestFunc: async (url, config, params) => {
          console.log("ajaxRequestFunc", url, config, params)
          if (!params.sorters) return;
          const {type, order, clause} = this.$route.query;
          let sort = params.sorters[0] || {dir: type, field: order}
          let {dir, field} = sort
          this.order = field
          this.type = dir
          let {page, size} = params
          this.offset = page - 1
          const data = await this.fetchData() || []
          return {data, last_page: Math.round(this.rowsCount / size)}
        },
        initialSort: [{column: this.order, dir: this.type}],
        data: [],
        columns: this.columns.map(c => {
          if (!columnWidthMap[c.type]) throw new Error(`Cant find type for ${c.type}`)
          return {
            title: c.name,
            field: c.name,
            vertAlign: 'middle',
            width: columnWidthMap[c.type],
            formatter: this.cellFormatter
          }
        })
      })
      this.tableData.setMaxPage(Math.round(this.rowsCount / this.limit))
    },

    cellFormatter (cell, formatterParams, onRendered) {
      const column = cell.getColumn().getField()
      return defaultValue(column, this.columnTypes[column], cell.getValue())
    },

    resetOrderTypeLimit() {
      const {database, table} = this.$route.params
      this.$store.commit('updateByKey', {database, table, key: 'limit', value: 20})
      this.$store.commit('updateByKey', {database, table, key: 'order', value: 1})
      this.$store.commit('updateByKey', {database, table, key: 'type', value: 'asc'})
      this.order = 1
      this.type = 'asc'
      this.limit = 20
      this.tableData.setData();
    },

    async fetchData() {
      const {database, table} = this.$route.params
      const orderType = `${this.order} ${this.type}`
      const limit = isNaN(this.limit) ? 20 : this.limit
      const offset = isNaN(this.offset) ? 0 : this.offset
      let request = {
        type: "rows",
        databaseName: database,
        tableName: table,
        order: orderType,
        limit: `${limit}`,
        offset: `${offset * limit}`,
        clause: this.clause
      }
      console.log("fetchData", request)
      const response = await Socket.sendRequest(request)
      console.log("<--- fetchData", response)
      this.rowsCount = response.data.count
      return response.data.rows
    },

    async applyFilters(filters) {
      const {table} = this.$route.params
      this.clause = sql.provider.selectQuery(table, filters)
      this.tableData.setData();
    },
  }
}
</script>