<template>
  <thead>
  <tr>
    <th v-for="column in columns"
        v-if="column" :key="column.__id"
        @click.prevent="orderClick(column.name)"
        :data-c="current"
        :data-eq="`${current === column.name}`"
        :data-type="orderType">
      <div style="display: flex; flex-direction: row">
        <span style="flex: 1 1 auto">{{ column.name }}</span>
        <i :class="type.toLowerCase() === 'asc' ? 'sort-descending' : 'sort-ascending'" v-if="current === column.name"/>
        <div v-if="current !== column.name" style="width: 14px; height: 2px"></div>
      </div>
    </th>
  </tr>
  </thead>
</template>

<script>
export default {
  props: {
    columns: Array,
    current: String,
    type: String,
    orderHandler: Function
  },

  mounted() {
    console.log(this.columns, this.current)
  },

  computed: {
    orderType() {
      return this.type.toLowerCase() === "asc" ? "desc" : "asc"
    }
  },

  methods: {
    orderClick(name) {
      this.orderHandler(this.orderType, name)
    }
  }
}
</script>