import sql from "@/utils/sql";


const defaultValue = (column, type, value) => {

    if (value === null || value === undefined) {
        return "<span class='light-gray-color'>NULL</span>"
    }

    if (column.type === "BLOB") return "BLOB"
    if (value) return value

    if (column === "is_locked") {
        console.log(column, value, !!value, type)
    }

    return sql.provider.defaultValues[type.toUpperCase()]
}

export default defaultValue