<template>
  <td>
    <div :class="`inner-border ${isInEditMode ? 'active' : ''}`" style="z-index: 1"/>
    <div
        v-html="value"
        v-if="!isInEditMode"
        @click.prevent="handleRowClick"
        style="z-index: 2; position:relative;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;"
    />
    <input
        type="text"
        v-if="isInEditMode"
        v-model="inputModel"
        class="table-row-value-edit"
        @keyup.enter.prevent="saveValue"
        @keyup.esc.prevent="isInEditMode = !isInEditMode"
        ref="updateValueInput"
        style="z-index: 2;">
  </td>
</template>

<script>
import defaultValue from "@/utils/default-value";

export default {

  props: ["id", "column", "row", "updateValue"],

  data() {
    return {
      allowEdit: false,
      isInEditMode: false,
      updatedValue: null
    }
  },

  computed: {
    value() {
      return defaultValue(this.column, this.row)
    },

    inputModel: {
      set(val) {
        this.updatedValue = val
      },
      get() {
        return this.value
      }
    }
  },


  methods: {
    handleRowClick() {
      if (!this.allowEdit) return
      this.isInEditMode = !this.isInEditMode
      this.$nextTick(() => this.$refs.updateValueInput.focus())
    },

    saveValue() {
      this.isInEditMode = !this.isInEditMode
      this.updateValue(this.id, this.column, this.updatedValue)
    }
  }
}
</script>