import WebSocketAsPromised from 'websocket-as-promised';

// const url = localStorage.getItem('serverUrl') || location.host
const url = "localhost:9001"
const wsp = new WebSocketAsPromised(`ws://${location.host}/ws`, {
    packMessage: data => JSON.stringify(data),
    unpackMessage: async data => JSON.parse(await data.text()),
    attachRequestId: (data, requestId) => ({...data, requestId}),
    extractRequestId: data => data && data.requestId
});

const open = async () => {
    await wsp.open()
    console.log("socket opened")
}

wsp.onClose.addListener(event => {
    console.log("onClose", event)
})

open()
export default wsp
