import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        selectedRow: null,
        tables: [],
        databases: [],
        tablesSidebarWidth: 250,
        isTablesSideBarHidden: false,
        isSqlLogsHidden: false,
        sqlLogsHeight: 200,
        tableFiltersHeight: 42
    },
    mutations: {
        setDatabases(state, databases) {
            state.databases = databases
        },

        setTables(state, tables) {
            state.tables = tables
        },

        updateByKey(state, payload) {
            const {value, database, table, key} = payload
            localStorage.setItem(`${database}_${table}_${key}`, value)
        },

        updateOffset(state, database, table, offset) {
            localStorage.setItem(`${database}_${table}_offset`, offset)
        },

        updateSqlLogsHeight(state, height) {
            state.sqlLogsHeight = height
        },

        updateTableFiltersHeight(state, height) {
            state.tableFiltersHeight = height
        },

        updateTablesSidebarWidth(state, width) {
            state.tablesSidebarWidth = width
        },

        toggleTablesSideBarHidden(state) {
            state.isTablesSideBarHidden = !state.isTablesSideBarHidden
            state.tablesSidebarWidth = state.isTablesSideBarHidden ? 1 : 250
        },

        toggleSqlLogsHidden(state) {
            state.isSqlLogsHidden = !state.isSqlLogsHidden
            state.sqlLogsHeight = state.isSqlLogsHidden ? 1 : 200
        }
    },
    actions: {},
    modules: {},

    getters: {
        getLimit: (state) => (database, table) => {
            let l = localStorage.getItem(`${database}_${table}_limit`)
            return parseInt(l || 20, 10)
        },

        getByKey: (state) => (database, table, key) => {
            return localStorage.getItem(`${database}_${table}_${key}`)
        },

        getFilters: (state) => (database, table) => {
            const cachedString = localStorage.getItem(`${database}_${table}_filters`)
            if (!cachedString) return []
            return JSON.parse(cachedString)
        },

        getTablePrimaryKey: (state) => (table) => {
            return state.tables
                .find(t => t.tableName === table)?.columns
                .find(c => c.pk === 1)?.name
        }
    }
})
