<template>
  <div class="table-filters" style="min-height: 42px;position:relative;">
    <div class="table-filter" v-for="(f, index) in filters">
      <select
          name="column"
          class="mr-0-5"
          v-model="f.column"
          @change="handleColumnChange(f)">
        <option
            :value="column.name"
            v-for="column in columns">{{ column.name }}
        </option>
      </select>

      <select
          name="filter"
          class="mr-0-5"
          v-model="f.operator"
          @change="handleOperatorChange(f)">
        <option
            :value="filter"
            v-for="filter in filterOperators">{{ filter }}
        </option>
      </select>
      <input
          type="text"
          v-model="f.value"
          :placeholder="f.placeholder"
          @input="validateInputValue(f)"
          class="mr-0-5"
      >
      <button
          class="btn-octicon mr-0-5"
          @click.prevent="addFilter"
      >+
      </button>
      <button
          :class="`btn-octicon mr-0-5`"
          @click.prevent="removeFilter(index)"
      >-
      </button>
      <button class="btn btn-primary" @click.prevent="apply" :disabled="!f.isButtonEnabled">Apply</button>
    </div>
    <button
        class="btn btn-primary"
        @click.prevent="addFilter"
        v-if="filters.length === 0"
        style="position: absolute;top: 50%;transform: translateY(-50%);right: 8px"
    >Add Filter</button>
  </div>
</template>

<script>
import sql from "@/utils/sql";
import Vue from 'vue'

export default {
  props: {
    database: String,
    table: String,
    columns: Array,
    applyFilters: Function,
  },

  data() {
    const operators = Object.keys(sql.provider.filterOperators)
    const filters = this.$store.getters.getFilters(this.database, this.table)
    return {
      filterOperators: operators,
      value: null,
      filters: filters
    }
  },

  watch: {
    filters() {
      const height = Math.max(42, this.filters.length * 42)
      this.$store.commit('updateTableFiltersHeight', height)
    }
  },

  methods: {

    handleOperatorChange(filter) {
      filter.placeholder = sql.provider.operatorInputPlaceholder[filter.operator]
      this.apply()
    },

    handleColumnChange(filter) {
      this.apply()
    },

    validateInputValue(filter) {
      filter.isButtonEnabled = sql.provider.operatorValueValidators[filter.operator].test(filter.value)
      if (filter.isButtonEnabled) {
        this.apply()
      }
    },

    removeFilter(index) {
      Vue.delete(this.filters, index)
      this.apply()
    },

    addFilter() {
      this.filters.push({
        value: null,
        column: this.columns[0]?.name,
        operator: this.filterOperators[0],
        placeholder: sql.provider.operatorInputPlaceholder[this.filterOperators[0]],
        isButtonEnabled: false
      })
    },

    apply() {
      this.applyFilters(this.filters)
      this.save()
    },

    save() {
      this.$store.commit('updateByKey', {
        database: this.database,
        table: this.table,
        key: 'filters',
        value: JSON.stringify(this.filters)
      })
    },
  }
}
</script>