
const selectedProvider = "sqlite"

const sqlite = {
    defaultValues: {
        "INTEGER": 0,
        "TEXT": "<span class='light-gray-color'>EMPTY</span>",
        "VARCHAR": "<span class='light-gray-color'>EMPTY</span>",
        "REAL": 0.0,
        "BLOB": 0
    },

    filterOperators: {
        "=": "=",
        "<>": "<>",
        "<": "<",
        ">": ">",
        "<=": "<=",
        ">=": ">=",
        "IN": "IN",
        "NOT IN": "NOT IN",
        "IS NULL": "IS NULL",
        "BETWEEN": "BETWEEN",
        "NOT BETWEEN": "NOT BETWEEN",
        "contains": "contains",
        "Not contains": "Not contains",
        "Has prefix": "Has prefix",
        "Has suffix": "Has suffix"
    },

    operatorInputPlaceholder: {
        "=": 'EMPTY',
        "<>": 'EMPTY',
        "<": 'EMPTY',
        ">": 'EMPTY',
        "<=": 'EMPTY',
        ">=": 'EMPTY',
        "IN": '1,2,3',
        "NOT IN": '1,2,3',
        "IS NULL": '',
        "IS NOT NULL": '',
        "BETWEEN": '1 AND 100',
        "NOT BETWEEN": '1 AND 100',
        "contains": 'Pattern',
        "Not contains": 'Pattern',
        "Has suffix": 'Pattern',
        "Has prefix": 'Pattern',
    },

    operatorString: {
        "=": (v) => `= '${v}'`,
        "<>": (v) => `<> '${v}'`,
        "<": (v) => `< '${v}'`,
        ">": (v) => `> '${v}'`,
        "<=": (v) => `<= '${v}'`,
        ">=": (v) => `>= '${v}'`,
        "IN": (v) => `IN (${v})`,
        "NOT IN": (v) => `NOT IN (${v})`,
        "IS NULL": () => `IS NULL`,
        "IS NOT NULL": () => `IS NOT NULL`,
        "BETWEEN": (v) => `BETWEEN ${v}`,
        "NOT BETWEEN": (v) => `NOT BETWEEN ${v}`,
        "contains": (v) => `LIKE '%${v}%'`,
        "Not contains": (v) => `NOT LIKE '%${v}%'`,
        "Has suffix": (v) => `LIKE '%${v}'`,
        "Has prefix": (v) => `LIKE '${v}%'`,
    },

    operatorValueValidators: {
        "=": /.+/,
        "<>": /.+/,
        "<": /.+/,
        ">": /.+/,
        "<=": /.+/,
        ">=": /.+/,
        "IN": /(.+?)(?:,|$)/,
        "NOT IN": /(.+?)(?:,|$)/,
        "IS NULL": /\s{0}/,
        "IS NOT NULL": /\s{0}/,
        "BETWEEN": /\w+\s(and|AND)\s\w+/,
        "NOT BETWEEN": /\w+\s(and|AND)\s\w+/,
        "contains": /.+/,
        "Not contains": /.+/,
        "Has suffix": /.+/,
        "Has prefix": /.+/
    },

    selectQuery(table, filters) {
        let query = ""
        let whereQueries = []
        for (let filter of filters) {
            if (!filter.value) continue;
            let q = sqlite.operatorString[filter.operator](filter.value)
            whereQueries.push(`("${filter.column} ${q}")`)
        }
        if (whereQueries.length === 0) return null
        query += ` ${whereQueries.join(" AND ").replace(/"/g, "")}`
        return query
    }
}

export default {
    provider: sqlite
}